.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.navbar {
  background-color: darkslategrey;
  padding-bottom: 20px;
}

.grid {
  display: grid;
  margin: 10px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 2rem;
  background: grey;
}
.box {
  transition: all 0.5s;
}
.box:hover {
  background: white;
}
.box img {
  max-width: 100px;
  height: auto;
  transition: all 0.3s;
  opacity: 1;
}

.box img:hover {
  opacity: 0.7;
}

input {
  height: 2rem;
  width: 50%;
}

.hero {
  display: flex;
  position: relative;
  text-align: center;
}
.hero img {
  padding-right: 40%;
  max-width: 50%;
  max-height: 400px;
}

.info {
  width: 30%;
  position: absolute;
  top: 30%;
  right: 20%;
  font-weight: 500;
}

.info strong {
  font-size: 2rem;
}

.overview {
  width: 100%;
  height: 100px;
  overflow: hidden;
  padding: 10px;
}

.empty {
  opacity: 0;
  animation-name: comein;
  animation-duration: 5s;
  animation-fill-mode: forwards;
}

@keyframes comein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media only screen and (max-width: 700px) {
  .info {
    position: absolute;
    right: 10%;
    font-size: 1rem;
  }

  .info strong {
    font-weight: normal;
    font-size: 1rem;
  }

  .overview {
    display: none;
  }

  .budget {
    display: none;
  }
}
